import './style.css'
import { push as Menu } from 'react-burger-menu'


export default function Header(){
    const infos = ["Sobre mim","Projetos","Contatos"]
    
    return(
        <header>
            <Logo/>
           
            <Menu right width={'50%'}
            noOverlay
            >
                 {infos.map(item=>{
                return <a key={item} href={`#${item}`}>{item}</a>
                        })}
            </Menu>
        </header>
    );
}
function Logo(){
    return(
        <div className='Logo'>
             <img alt='Logo' src="" />
        </div>
       
    )
}
